@charset "UTF-8";
@import url("https://fonts.cdnfonts.com/css/louis-george-cafe");
@import url("https://fonts.googleapis.com/css2?family=Playfair+Display:ital,wght@0,400;0,500;0,600;0,700;0,800;0,900;1,400;1,500;1,600;1,700;1,800;1,900&display=swap");
* {
  font-family: "Louis George Café";
}

body {
  width: 100%;
  background-color: #F5EFF0;
  box-sizing: border-box;
}

.background-block__header {
  background-color: white;
}
.background-block__main {
  background-color: white;
}
.background-block__footer {
  background-color: #F5EFF0;
}
.background-block header {
  margin: auto;
  max-width: 1200px;
}
.background-block main {
  margin: auto;
  max-width: 100%;
}
.background-block footer {
  margin: auto;
  max-width: 1200px;
}

header {
  display: flex;
}
header .logo {
  width: 200px;
}
header nav ul {
  display: flex;
  list-style: none;
}
header nav ul li {
  margin: 1em;
}
header nav ul li a {
  font-family: "Playfair Display";
  text-decoration: none;
  color: black;
}

footer {
  display: flex;
  justify-content: space-between;
}
footer ul {
  display: flex;
  list-style: none;
  margin: 0;
}
footer ul li {
  margin: 0em 1em;
}
footer ul li a {
  font-family: "Playfair Display";
  text-decoration: none;
  color: black;
}

.introduction {
  background-image: url("assets/1_Accueil/banner.jpg");
  background-size: auto 100%; /* Ajuste la largeur à 100% et la hauteur automatiquement */
  background-repeat: no-repeat;
  background-position: center center; /* Centre l'image dans le bloc */
  width: 100%;
  height: 800px;
  z-index: 1;
  display: flex; /* Utilise Flexbox pour le premier bloc */
  align-items: center; /* Centre verticalement */
  justify-content: center; /* Centre horizontalement */
}
.introduction .alpha-grey {
  width: inherit;
  height: inherit;
  position: absolute;
  background-color: rgba(40, 40, 40, 0.6);
  z-index: 2;
}
.introduction__welcome {
  position: relative;
  z-index: 3;
  color: white;
  vertical-align: middle;
  display: flex; /* Utilise Flexbox pour le premier bloc */
  align-items: center; /* Centre verticalement */
  justify-content: center; /* Centre horizontalement */
  flex-wrap: wrap;
}
.introduction__welcome h1 {
  font-family: "Playfair Display";
  width: 100%;
  text-align: center;
  font-size: 62px;
  text-shadow: 1px 1px 8px rgb(0, 0, 0);
}
.introduction__welcome p {
  width: 60%;
  text-align: center;
  font-size: 28px;
  font-weight: 500;
  line-height: 1em;
  text-shadow: 1px 1px 8px rgb(0, 0, 0);
}

.works {
  background: #f6f7fa;
  width: 100%;
  height: fit-content;
}
.works p {
  text-align: center;
  color: #313131;
  font-weight: 600;
  line-height: 1;
  font-size: 26px;
}
